.loading-screen {
  z-index: 9999;
  background-color: #fff;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  animation: 7s forwards fadeOut;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

video {
  width: 80%;
  max-width: 400px;
}

@keyframes fadeOut {
  0% {
    opacity: 1;
  }

  100% {
    opacity: 0;
    visibility: hidden;
  }
}
